<template>
    <div class="container">
        <LeftMenu></LeftMenu>
        <RouterView></RouterView>
    </div>
</template>

<script>
import LeftMenu from "@/components/dashboard/LeftMenu.vue";
import { socket } from "@/socket";

export default {
    name: "ConfigMatricula",
    data() {
        return {
            numMatricula: '',
        };
    },
    methods: {
        listenMatricula() {
            socket.on('matricula', (arg) => {
                this.numMatricula = arg;
                this.$store.commit('addMatricula', this.numMatricula);
                console.log(this.numMatricula);
            });
        },
    },
    mounted() {
        this.listenMatricula();
    },
    components: { LeftMenu}
};
</script>

